//
// VARIABLES
//-----------------------------------------------------------------------

// COLORS
//---------------------------

$color-black:             #000;
$color-gray-1:            #333;
$color-gray-2:            #8A8A8A;
$color-gray-3:            #BFBFBF;
$color-gray-4:            #E0E0E0;
$color-gray-5:            #F5F5F5;
$color-white:             #fff;

$color-brand-primary:     #6F19DD;
$color-brand-secendary:   #FA8E6B;

$color-success:           #27AE60;
$color-info:              #2D9CDB;
$color-warning:           #F2C94C;
$color-danger:            #EB5757;

$color-body-background:   #E0E0E0;
$color-text:              $color-gray-1;

$color-link:              $color-brand-primary;
$color-link-hover:        darken($color-brand-primary, 10%);


// TYPOGRAPHY
//---------------------------

$text-font-family-sans-serif:   'IBM Plex Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
$text-font-family-serif:        'Playfair Display', 'Georgia' serif;

$text-font-family:              $text-font-family-sans-serif;

$text-font-size:                1.8rem;
$text-line-height:              1.625;

$text-font-size-md:             1.8rem;
$text-line-height-md:           1.625;

$text-font-weight:              300;
$text-font-weight-heavy:        500;


// BOX SHADOWS
//---------------------------

$box-shadow:              0 0 1px rgba(#000, 0.6);
$box-shadow-high:         0 5px 10px -3px rgba(#000, 0.4);
$box-shadow-higher:       0 10px 25px -4px rgba(#000, 0.4);
$box-shadow-highest:      0 20px 55px -8px rgba(#000, 0.4);


// BORDERS
//---------------------------

$border-color:            $color-gray-4;
$border-width:            1px;
$border-radius:           .4rem;


// DISABLED
//---------------------------

$disabled-opacity:        .5;


// SPACING
//---------------------------

$spacing-base:            0.8rem;

$spacing-tiny:            $spacing-base;        // 8px
$spacing-xsmall:          $spacing-base * 2;    // 16px
$spacing-small:           $spacing-base * 3;    // 24px
$spacing-medium:          $spacing-base * 4;    // 32px
$spacing-large:           $spacing-base * 6;    // 48px
$spacing-xlarge:          $spacing-base * 8;    // 64px
$spacing-super:           $spacing-base * 10;   // 80px

// Medium variables?


// GRID
//---------------------------

$grid-gutter:             $spacing-xsmall;
$grid-gutter-md:          $spacing-medium;


// BREAKPOINTS
//---------------------------

$breakpoint-small-max:    "only screen and (max-width : 899px)";
$breakpoint-medium:       "only screen and (min-width : 900px)";
$breakpoint-large:        "only screen and (min-width : 1400px)";


// Z-INDEXES
//---------------------------

$zindex-menu:             100;
$zindex-navbar:           200;
$zindex-modal:            300;
$zindex-alert:            400;
